	computed: mapState(['forcedLogin', 'hasLogin', 'phone']),
	onLoad() {

		let uniIdToken = uni.getStorageSync('uniIdToken')
		this.login(uni.getStorageSync('phone'))
		if (uniIdToken) {
			uniCloud.callFunction({
				name: 'user-center',
				data: {
					action: 'checkToken',
				},
				success: (e) => {

					console.log('checkToken success', e);

					if (e.result.code > 0) {
						//token过期或token不合法，重新登录
						if (this.forcedLogin) {
							uni.reLaunch({
								url: '../login/login'
							});
						} else {
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				},
				fail(e) {
					uni.showModal({
						content: JSON.stringify(e)
					})
				}
			})
		} else {
			this.guideToLogin()
		}
	},
	methods: {
		...mapMutations(['login']),
		guideToLogin() {
			if (this.forcedLogin) {
